@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400&display=swap);
body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: #20201f;
}

.App {
  text-align: center;
  height: 100%;
  background-color: #20201f;
}

.nav-container {
  background-color: rgb(32, 31, 30);
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
}

@media (min-width: 700px) {
  .nav-container {
    padding: 0% 4%;
  }
}

/* ==== Tech Hub IIT Tirupati BRAND NAME ==== */

.main-web-name {
  font-size: 180%;
  background-image: linear-gradient(
    90deg,
    rgb(0, 185, 214),
    rgb(3, 167, 177),
    rgb(3, 163, 137)
  );
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

/* ==== NAVIGATION LINKS ==== */

.navigation {
  box-sizing: border-box;
}

.navigation-link {
  display: inline-block;
  position: relative;
}

.navigation-link:link::before {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    90deg,
    rgba(0, 188, 242, 1),
    rgba(123, 163, 244, 1),
    rgba(180, 160, 255, 1)
  );
  transform-origin: bottom right;
  transition: transform ease-in-out 0.8s;
  -webkit-transition: transform ease-in-out 0.8s;
  -moz-transition: transform ease-in-out 0.8s;
  -ms-transition: transform ease-in-out 0.8s;
  -o-transition: transform ease-in-out 0.8s;
}

.navigation-link:active::before {
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform-origin: bottom left;
}

.navbar-dark .navbar-toggler-icon {
  background-image: url(/frontend-team/static/media/navbarBurgerMenuIcon.fed70ed7.png);
}

.navbar-dark .navbar-toggler {
  border-color: rgba(32, 31, 30, 0);
}
.navbar-dark .navbar-toggler:hover {
  outline: none;
}

/* ==== MEDIA QUERIES ==== */

@media only screen and (max-width: 960px) {
  .main-web-name {
    font-size: 170%;
  }
}

@media only screen and (max-width: 768px) {
  .main-web-name {
    font-size: 160%;
  }
}

@media only screen and (max-width: 480px) {
  .main-web-name {
    font-size: 140%;
  }
}

.border-div-calendar,
.border-div-calendar-no-hover {
  width: 100%;
  height: 100%;
  padding: 2px;
  background: linear-gradient(to bottom, #00bcf2, #b4a0ff);
}

.border-div-calendar:hover {
  padding: 4px;
}

.cell-calendar {
  width: 100%;
  height: 110px;
  padding: 0.4rem 0.8rem;
}

.top-row {
  margin-top: 1rem;
}
.bottom-row {
  margin-bottom: 1rem;
}

.no-event-cell {
  height: 100%;
  width: 100%;
  background: #605e5d;
  position: relative;
  box-shadow: inset 0px 0px 9px -2px rgba(0, 0, 0, 1);
}

.not-present-month {
  background: #323131;
  height: 100%;
  width: 100%;
}

.date-calendar {
  position: absolute;
  bottom: 0;
  right: 0.3rem;
  color: #e1dfde;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 1.2rem;
}

.th-calendar {
  width: 190px;
  font-family: 'Roboto Slab', sans-serif;

  font-size: 1.1rem;
  min-width: 130px;
  letter-spacing: 0.1em;
}

.monthName-calendar {
  display: inline-block;
  color: white;
  height: 100%;
  position: relative;
  width: 100%;
}

.heading-calendar {
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 4em;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  margin: 2% auto;
  margin-bottom: 0;

  background: linear-gradient(to right, #00bcf2, #b2a0ff);
  letter-spacing: 0.015em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.month-name-calendar {
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 2em;
  margin-bottom: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;

  /* identical to box height */
  background: linear-gradient(to right, #00bcf2, #b2a0ff);
  letter-spacing: 0.015em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 1000px) {
  .table-calendar {
    overflow: scroll;
  }
}

@media (max-width: 700px) {
  .calendar-container {
    display: none;
  }
}

@media (min-width: 700px) {
  .HomePage {
    margin: 0 5%;
  }
}

.HomePage {
  margin: 0 2%;
}

.image-div {
  height: 100%;
  width: 100%;
}

.content-div {
  height: 100%;

  width: 100%;

  background-color: #20201f;

  padding: 0.7rem;
  overflow: scroll;
}

.border-div {
  display: inline-block;
  width: 50%;
  height: 90%;
  background: linear-gradient(to bottom, #00bcf2, #b4a0ff);
  padding: 1px;
  color: white;
}

.event-heading-carousel {
  font-family: 'Roboto Slab';
  margin-bottom: 0.2rem;
  font-weight: 300;
  font-size: 3em;

  letter-spacing: 0.015em;

  color: #e1dfdd;
}

.event-description-carousel {
  font-family: 'Roboto Slab';

  font-weight: 300;
  font-size: 1.3em;

  letter-spacing: 0.015em;

  color: #e1dfdd;
}

.carousel-root {
  display: flex;
  justify-content: flex-start;
  height: 300px;
  overflow: hidden;
}

.heading-carousel {
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 4rem;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  margin: 2% auto;

  background: linear-gradient(to right, #00bcf2, #b2a0ff);
  letter-spacing: 0.015em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (min-width: 700px) {
  .mobile-carousel {
    display: none;
  }
  .carousel-root {
    height: 420px;
  }
}

@media (max-width: 700px) {
  .heading-carousel {
    font-size: 40px;
  }
  .desktop-carousel {
    display: none;
  }
  .carousel {
    padding-bottom: 50px;
  }
  .carousel-indicators li {
    width: 20px;
    height: 1px;
    background: #00bcf2;
    border: 0.5px solid #00bcf2;
  }
  .carousel-indicators .active {
    background: #b4a0ff;
    border: 0.5px solid #b4a0ff;
  }
}

@media (min-width: 700px) {
  .carousel-indicators li {
    height: 8px;
    width: 8px;
    margin: 2px;
    border: 2px solid #00bcf2;
    border-radius: 50%;
    display: inline-block;
    opacity: 1;
    background-color: inherit;
  }
  .carousel-indicators .active {
    background: #b4a0ff;
    border: 2px solid #b4a0ff;
  }
  .carousel-indicators {
    margin-bottom: 0;
    bottom: 5%;
  }
}

.border-div-button {
  background: linear-gradient(to right, #00bcf2, #b4a0ff);
  padding: 1px;
  width: 170px;
  border-radius: 2px;
}

.Button {
  font-family: 'Roboto Slab';

  background: #20201f;
  color: white;
  text-align: center;
  padding: 3px 0;
}

.Button:hover {
  background: #e1dfdd;
  color: #201f1e;
  box-shadow: inset 0px 0px 9px -2px rgba(0, 0, 0, 1);
}

.image-div-featured {
  display: inline-block;
  width: 40%;
  height: 90%;

  color: white;
}

.border-root-featured {
  margin-top: 4px;
  background: linear-gradient(to right, #00bcf2, #b4a0ff, #01b295);
  padding: 4px;
  border-radius: 6px;
  color: white;
}

.carousel-root-featured {
  display: flex;
  justify-content: flex-start;
  height: 45vw;
  overflow: hidden;
}

.content-div-featured {
  height: 100%;

  width: 70%;

  background-color: #20201f;
}

.featured-heading {
  padding: 0%;
  font-family: 'Roboto Slab';

  margin: 4% 0 0 10%;
  font-size: 7vw;
  font-weight: 300;
  background: linear-gradient(to right, #00bcf2, #01b295);
  letter-spacing: 0.015em;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  letter-spacing: -0.015em;
}

.button-div-featured-desktop {
  margin: 7% 0 0 10%;
}

.button-div-featured-mobile {
  display: none;
}

.featured-description {
  font-family: 'Roboto Slab';
  padding: 0%;
  margin: 4% 0 0 10%;
  font-size: 1.5vw;
  font-weight: 300;
  background: linear-gradient(to right, #00bcf2, #01b295);
  letter-spacing: 0.015em;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: auto;
  text-align: left;

  letter-spacing: -0.015em;
}

.image-featured {
  margin: 40% 4% auto 0;
}

@media (max-width: 700px) {
  .carousel-root-featured {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: auto;
    min-height: 700px;
  }
  .content-div-featured {
    min-height: 50%;
    height: auto;

    width: 100%;
  }
  .featured-heading {
    margin: 4% 0 0 7%;
    font-size: 50px;
  }
  .featured-description {
    margin: 4% 0 0 7%;
    font-size: 15px;
  }

  .image-div-featured {
    display: block;
    width: 100%;
    height: auto;
    margin-top: 10%;
    color: white;
    padding: 5%;
  }

  .image-featured {
    margin: 0 auto;
  }

  .button-div-featured-desktop {
    display: none;
  }
  .button-div-featured-mobile {
    display: block;
    display: flex;
    justify-content: center;
    margin-top: 10%;
  }
}

.border-div-button-featured {
  background: linear-gradient(to right, #00bcf2, #01b295);

  padding: 0.5%;
  width: 50%;
  border-radius: 6px;
}

.Button-featured {
  font-family: 'Roboto Slab';

  background: #20201f;

  padding: 3%;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Button-text-featured {
  font-weight: 200;
  font-size: 2vw;
  background: linear-gradient(to right, #00bcf2, #01b295);
  letter-spacing: 0.015em;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.Button-featured:hover {
  background: #e1dfdd;
  color: #201f1e;
  box-shadow: inset 0px 0px 9px -2px rgba(0, 0, 0, 0.5);
}

.Button-featured:hover span {
  background: inherit;

  -webkit-text-fill-color: #201f1e;
}

@media (max-width: 700px) {
  .Button-text-featured {
    font-size: 5vw;
  }
  .border-div-button-featured {
    padding: 1px;
    width: 200px;
    border-radius: 2px;
  }
  .Button-featured {
    padding: 2%;
    border-radius: 2px;
  }
  .Button-text-featured {
    font-weight: 400;
  }
}

